import React from "react";

export const LandingBanner = (props) => {
    return (
        <section className="banner-section col-12">
            <div className="banner">
                <h1>VietCourses cung cấp các khóa học online đa lĩnh vực</h1>
                <h2>
                    Chỉ cần vài bước đăng ký tài khoản, bạn có thể học mọi lúc, mọi nơi cùng các giảng viên hàng đầu đến
                    từ các trường đại học trong nước.
                </h2>
                <button id="signup2">Đăng ký ngay</button>
            </div>
            <div className="backgr"></div>
            <div className="backgrImg"></div>
        </section>
    );
};
export default LandingBanner